import React from 'react';
import BugForm from './bugForm.js';
import BugList from './BugList.js'
import { Container, Grid } from '@material-ui/core';

class Bug extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            isEditBug: false
        }
    }
    render() {
        return (
            <Container>
                <Grid container spacing={2}>
                    <Grid item xs={6}>
                        <BugForm isEdit={this.state.isEditBug} cancelEdit ={ () => this.setState({isEditBug: false }) } />
                    </Grid>
                    <Grid item xs={6}>
                        <BugList editBug={ (val) => this.setState({ isEditBug: val}) } />
                    </Grid>
                </Grid>                
            </Container>
        )
    }
}

export default Bug;