import React, { useState, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import Axios from 'axios';
import Bug from '../bug'
import AlertPopup from '../widgets/alertPopup.js';
import './custom.scss';
import { FormControl, TextField, Button, IconButton, Link } from '@material-ui/core';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import { SET_ALERT_POPUP } from '../../store/types.js';
import { Grid } from '@material-ui/core';


export default function Layout() {

    const [isSignedIn, setSignedIn] = useState(false);
    const [isForgetPassword, setIsForgetPassword] = useState(false);

    useEffect(() => {
        Axios.get('/authentication').then(({status}) => { 
            setSignedIn(status === 200)
        })
    })

    const memberLogin = <div class="member-login-container">
                            <div class="top">
                                <img src="logo.png" />                                
                            </div>                            
                            <div class="bottom">
                                { isForgetPassword ? <ForgetPassword isForgetPassword={(value) => setIsForgetPassword(value)} /> : <MemberLogin isSignedIn={(value) => setSignedIn(value)} isForgetPassword={(value) => setIsForgetPassword(value)} /> }
                            </div>
                        </div>

    const handleLogout = () => {
        Axios.get('/authentication/logout').then(() => {
            setSignedIn(false)
        })
    }

    const memberLayout =<div class="member-layout-container">
                            <div class="header-bar">
                                <div class="top-left">
                                    <img src="logo-title.png" height="50"/>
                                </div>
                                <div class="top-right">
                                    <IconButton onClick={handleLogout} style={{color: 'white'}} title="Logout">
                                        <ExitToAppIcon></ExitToAppIcon>
                                    </IconButton>
                                </div>
                            </div>
                            <div class="content">
                                <Bug />
                            </div>
                            <div class="footer-bar" style={ { padding: '3px', color: 'white', textAlign: 'right' } } >
                                Developed by Vijay Wilson J
                            </div>
                        </div>                       
    
    return (
        <React.Fragment>
            { isSignedIn ? memberLayout : memberLogin }
            <AlertPopup />                            
        </React.Fragment>
    )
}

function MemberLogin(props) {

    const [username, setUsername] = useState(null);
    const [password, setPassword] = useState(null);

    const [ showAlert ] = useShowAlert()

    const handleSignup = () => {
        Axios.post('/authentication/signup', {username, password}).then(({data}) => {
            showAlert(data)
        })
    }

    const handleLogin = () => {
        Axios.post('/authentication/login', {Username: username, Password: password}).then(({ data }) => {
            const { data: isAuthenticated } = data
            if(!isAuthenticated)
                showAlert(data)
            else
            props.isSignedIn(true)
        });
    }

    return (
        <div>
            <FormControl fullWidth={true} margin="dense" >
                <TextField type="text" name="username" onChange={(evt) => setUsername(evt.target.value)} label="UserName" required={true} variant="outlined" size="small"  />
            </FormControl>
            <FormControl fullWidth={true} margin="dense">
                <TextField type="password" name="password" onChange={(evt) => setPassword(evt.target.value)} label="Password" required={true} variant="outlined" size="small" />
            </FormControl>
            <FormControl fullWidth={true} margin="dense" >
                <Link style={{textAlign: 'left'}} component="button" variant="body2" onClick={() => { props.isForgetPassword(true) }}> Forget Password </Link>
            </FormControl>
            <FormControl>
                <Grid container>
                    <Grid item xs={6}>
                        <Button onClick={handleLogin} variant="contained" color="primary">Login</Button>
                    </Grid>
                    <Grid item xs={6}>
                        <Button onClick={handleSignup} variant="contained" color="primary">SignUp</Button>
                    </Grid>
                </Grid>
            </FormControl>
        </div>
    )
}

function ForgetPassword(props) {

    const [email, setEmail] = useState(null);
    const [verificationCode, setVerificationCode] = useState(null);
    const [newPassword, setNewPassword] = useState(null);

    const [ showAlert ] = useShowAlert()

    const handleForgetPasswordCancel = () => {
        setEmail(null);
        setVerificationCode(null);
        setNewPassword(null);
        props.isForgetPassword(false);
    }

    const handleSendVerificationCode = () => {
        const formData = new FormData();
        formData.append("email", email);
        Axios.post('/authentication/sendverificationcode', formData).then(({data}) => {
            showAlert(data)            
        })
    }

    const handleUpdatePassword = () => {
        const payload = { email, newPassword, verificationCode }
        Axios.post ('/authentication/updatepassword', payload).then(({data}) => {
            showAlert(data)
            const { data: IsUpdated } = data
            if(IsUpdated)
                handleForgetPasswordCancel();
        })
    }

    return (
        <div>
            <FormControl fullWidth={true} margin="dense" >
                <Grid container spacing={2}>
                    <Grid item xs>
                        <TextField type="text" name="email" onChange={(evt) => setEmail(evt.target.value)} fullWidth={true} label="Email" required={true} variant="outlined" size="small"  />
                    </Grid>
                    <Grid item>
                        <Button variant="contained" color="secondary" onClick={handleSendVerificationCode}>Send</Button>
                    </Grid>
                </Grid>
            </FormControl>
            <FormControl fullWidth={true} margin="dense">
                <TextField type="password" name="verificationCode" onChange={(evt) => setVerificationCode(evt.target.value)} label="Verification Code" required={true} variant="outlined" size="small" />
            </FormControl>
            <FormControl fullWidth={true} margin="dense">
                <TextField type="password" name="newPassword" onChange={(evt) => setNewPassword(evt.target.value)} label="New Password" required={true} variant="outlined" size="small" />
            </FormControl>
            <FormControl>
                <Grid container spacing={2}>
                    <Grid item xs={6}>
                        <Button variant="contained" color="primary" onClick={handleUpdatePassword}>Update</Button>
                    </Grid>
                    <Grid item xs={6}>
                        <Button onClick={handleForgetPasswordCancel} variant="contained" color="primary">Cancel</Button>
                    </Grid>
                </Grid>
            </FormControl>
        </div>
    )
}

const ResultType = {
    Success: 1,
    Error: 2,
    Warning: 3
}

function useShowAlert() {
    const dispatch = useDispatch();
    const showAlert = (data) => {
        if(data) {
            const {resultType, message, messages} = data
            dispatch({ type: SET_ALERT_POPUP, payload: { 
                open: true, 
                type: resultType,
                title: resultType === ResultType.Success ? 'Success' : resultType === ResultType.Error ?  'Error' : 'Warning', 
                content: message || messages
            }})
        }
    }

    return [ showAlert ]
}