import React from 'react';
import { connect } from 'react-redux';
import { RESET_BUG, SET_BUG, REMOVE_EXISTING_BUG_FILE } from  '../../store/modules/bugs/types.js'
import { createBug, updateBug } from '../../store/modules/bugs/actions.js'
import { Typography, FormControl, TextField, InputLabel, Select, MenuItem, Input, Button, GridList, GridListTile, GridListTileBar, IconButton  } from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';
import Notification from '../widgets/notification.js';


class BugForm extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            bug: this.props.bug,
            formFiles: []
        }

        this.handleInputChange = this.handleInputChange.bind(this);
        this.handleFileChange = this.handleFileChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleCancelEdit = this.handleCancelEdit.bind(this);
    }

    handleInputChange(event) {
        const name = event.target.name;
        const value = event.target.value;

        let { bug } =  this.state;
        bug = { ...bug, [name]: value };
        this.setState({ bug });
    }

    handleFileChange(event){
        const formFiles = event.target.files;
        this.setState({ formFiles });
    }

    handleSubmit(event) {
        event.preventDefault();

        let { bug } =  this.state;
        this.props.setBug(bug);

        if(this.props.isEdit)
            this.props.updateBug(this.state.formFiles);
        else            
            this.props.createBug(this.state.formFiles);


        // reset file
        // const fileInputKey = this.state.fileInputKey;
        // this.setState({fileInputKey: fileInputKey === 0 ? 1 : 0, formFiles: []});
    }       

    handleCancelEdit() {
        this.props.resetBug();
        this.props.cancelEdit();
    }

    componentDidUpdate(prevProps) {        
        if(this.props.bug.title !== prevProps.bug.title)
            this.setState((state, props) => ({bug: { ...props.bug, title: props.bug.title } }))

        if(this.props.bug.bugSoftwareTypeId !== prevProps.bug.bugSoftwareTypeId)
            this.setState((state, props) => ({bug: { ...props.bug, bugSoftwareTypeId: props.bug.bugSoftwareTypeId } }))

        if(this.props.bug.recreationSteps !== prevProps.bug.recreationSteps)
            this.setState((state, props) => ({bug: { ...props.bug, recreationSteps: props.bug.recreationSteps } }))

        if(this.props.bug.aboutSoftware !== prevProps.bug.aboutSoftware)
            this.setState((state, props) => ({bug: { ...props.bug, aboutSoftware: props.bug.aboutSoftware } }))

        if(this.props.bug.comment !== prevProps.bug.comment)
            this.setState((state, props) => ({bug: { ...props.bug, comment: (props.bug.comment) ? props.bug.comment : ''  } }))
    }


    render() {
        let button;

        if(this.props.isEdit){
            button = <div>
                        <Button onClick={this.handleCancelEdit} varaint="contained">Cancel</Button> 
                        <Button onClick={this.handleSubmit} type="submit" variant="contained" color="primary">Update</Button>
                     </div>
        }
        else{
            button = <div><Button onClick={this.handleSubmit} variant="contained" color="primary">Create</Button></div>
        }
        return (
            <div>
                <form autoComplete="off" noValidate>
                    <FormControl fullWidth={true} margin="dense" >
                        <TextField label="Title" required={true} variant="outlined" size="small" type="text" name="title" value={this.state.bug.title} onChange={this.handleInputChange} />
                    </FormControl>
                    <FormControl fullWidth={true} margin="dense">
                        <TextField label="Recreation Steps" required={true} variant="outlined" multiline rows={3} name="recreationSteps" value={this.state.bug.recreationSteps} onChange={this.handleInputChange} />
                    </FormControl>
                    <FormControl fullWidth={true} margin="dense">
                        <Input inputProps={{multiple: true, accept: "image/*"}} type="file" name="bugFiles" key={this.state.fileInputKey} onChange={this.handleFileChange}/>
                        <RenderBugFiles existingBugFiles={this.props.existingBugFiles} removeExistingBugFile={this.props.removeExistingBugFile} />
                    </FormControl>                    
                    <FormControl fullWidth={true} margin="dense">
                        <TextField label="About Software" required={true} variant="outlined" multiline rows={3} name="aboutSoftware" value={this.state.bug.aboutSoftware} onChange={this.handleInputChange} />
                    </FormControl>
                    <FormControl fullWidth={true} margin="dense">
                        <InputLabel htmlFor="bug-software-type-id">Software Type</InputLabel>
                        <Select label="Software Type" required={true} labelId="bug-software-type-id" name="bugSoftwareTypeId" value={this.state.bug.bugSoftwareTypeId} onChange={this.handleInputChange} >
                            {this.props.bugSoftwareTypes.map(item => <MenuItem key={item.id} value={item.id}>{item.label}</MenuItem>)}
                        </Select>
                    </FormControl>
                    <FormControl fullWidth={true} margin="dense">
                        <TextField label="Comment" variant="outlined" multiline rows={3} name="comment" value={this.state.bug.comment} onChange={this.handleInputChange} />
                    </FormControl>                    
                    <FormControl fullWidth={true} margin="dense" style={{textAlign: "right"}}>
                        {button}                        
                    </FormControl>
                </form>
                <Notification vertical="bottom" horizontal="left"/>
            </div>
        )
    }
}

function RenderBugFiles(props) {    
    let gridList = [];
    if(props.existingBugFiles && props.existingBugFiles.length > 0) {
        gridList = props.existingBugFiles
                        .filter(bugFile => bugFile.filePath)
                        .map(bugFile => (
                            <GridListTile key={bugFile.bugFileId}>
                                <img src={bugFile.filePath} alt="[Resource Not Found]" />
                                <GridListTileBar actionIcon={<IconButton color="secondary"><DeleteIcon onClick={() => props.removeExistingBugFile(bugFile.bugFileId) } /></IconButton>}/>
                            </GridListTile> 
                            ));        
    }
    return (
        <GridList>
            {gridList}
        </GridList>
    )
}

function mapStateToProps(state) {
    return {
        bug: state.bugReducer.bug,
        existingBugFiles: state.bugReducer.existingBugFiles,
        bugSoftwareTypes: state.bugReducer.BugSoftwareTypes,
        notificationOptions: state.bugReducer.notification
    }
}

function mapDispatchToProps(dispatch) {
    return {
        setBug: (bug) => dispatch({ type: SET_BUG, payload: bug }),
        resetBug: () => dispatch({ type: RESET_BUG }),
        createBug: (formFiles) => dispatch(createBug(formFiles)),
        updateBug: async(formFiles) => await dispatch(updateBug(formFiles)),
        removeExistingBugFile: (bugFileId) => dispatch({ type: REMOVE_EXISTING_BUG_FILE, payload: bugFileId })
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(BugForm);